<template>
    <div class="page">
        <div class="containner">
            <breadCrumb :data="navData"></breadCrumb>
            <div class="contain">
                <el-form ref="form" :model="form" label-width="120px">
                    <el-form-item label="实验名称" :rules="{ required: true }">
                        <el-input v-model="form.experimentName"></el-input>
                    </el-form-item>
                    <el-form-item label="实验简介">
                        <el-input v-model="form.experimentIntro"></el-input>
                    </el-form-item>
                    <el-form-item label="实验详情">
                        <tinymceEditor v-model="form.experimentText"></tinymceEditor>
                    </el-form-item>
                    <el-form-item label="试验状态" prop="resource">
                        <el-radio-group v-model="form.experimentStatus">
                            <el-radio label="0">解锁</el-radio>
                            <el-radio label="1">未解锁</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="设置解锁阙值" prop="resource" v-if="form.experimentStatus==1">
                        <el-radio-group v-model="form.isQuezhi">
                            <el-radio label="0">是</el-radio>
                            <el-radio label="1">否</el-radio>
                        </el-radio-group>
                        <el-form-item v-if="form.isQuezhi==0">
                            <el-input v-model="form.valve" style="width:120px;"></el-input>
                            <el-alert
                                title="提示：当提交实验的人数占比（班级人数）大于阈值时，将自动解锁下一个实验"
                                type="info" show-icon style="line-height: 1em;margin-top:1em;">
                            </el-alert>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item label="设置实验封面">
                        <el-upload
                        class="avatar-uploader"
                        action="https://jsonplaceholder.typicode.com/posts/"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <div class="uploadBox">
                            <img v-if="imageUrl" :src="imageUrl" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </div>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="实验答案">
                        <tinymceEditor v-model="form.experimentAnswerContent"></tinymceEditor>
                    </el-form-item>
                    <el-form-item label="设置答案阙值" prop="resource">
                        <el-radio-group v-model="form.isAnswerQuezhi">
                            <el-radio label="0">是</el-radio>
                            <el-radio label="1">否</el-radio>
                        </el-radio-group>
                        <el-form-item v-if="form.isAnswerQuezhi==0">
                            <el-input v-model="form.punishment" style="width:120px;"></el-input>
                            <el-alert
                                title="提示：当学生点击查看参考答案时，最终实验成绩=教师打分*阈值比例"
                                type="info" show-icon style="line-height: 1em;margin-top:1em;">
                            </el-alert>
                        </el-form-item>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">确定</el-button>
                        <el-button>取消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
import TinymceEditor from '@/components/tinymce-editor'
import breadCrumb from '@/components/breadCrumb'
export default {
    props: {

    },
    data() {
        return {
            // editorHeight: "70vh", :height="editorHeight"
            navData:[
                {
                    name: "实验列表",
                    path: `/admin/courseList/experiment/${this.$route.params.courseId}`
                },{
                    name: "添加实验",
                    path: ""
                }
            ],
            form: {
                experimentName: '',
                experimentIntro: '',
                experimentText: '',
                experimentStatus: '0',
                isQuezhi:'0',
                isAnswerQuezhi:'0',
                valve:'',
                punishment:'',
	            experimentAnswerContent:'',
                experimentDetailFile:[],
                experimentAnswerFile:[],
            },
            imageUrl: '',
        };
    },
    computed: {

    },
    created() {

    },
    mounted() {

    },
    watch: {

    },
    methods: {
      onSubmit() {
        this.$http.post(`/teaching/teacher/experiment/add`,{
            experimentName: '',
            courseId:this.$route.params.courseId,
            experimentIntro: '',
            experimentText: '',
            experimentStatus: '0',
            valve:'',
            punishment:'',
            experimentAnswerContent:'',
            experimentDetailFile:[],
            experimentAnswerFile:[],
        }).then((res) => { 
            if(res.data.code == "0"){
                this.$message({
                    message: "成功新增实验！",
                    type: 'success'
                });
                this.$router.push('/admin'); 
            }else if (res.data.code == "1") {
                this.$router.push('/login'); 
            }else{
                this.$message({
                    message: res.data.msg,
                    type: 'error'
                });
            }
        })
        .catch(function (error) {
            console.log(error)
        })
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
    },
    components: {
        breadCrumb,
        TinymceEditor
    },
};
</script>

<style scoped lang="scss">
.page{
    background: #F4F4F4;
    .containner{
        width: 60%;
        margin:0 auto;
        padding-top: 20px;
        .contain{
            background: white;
            padding: 2em;
            .uploadBox{
                border: 1px dashed #d9d9d9;
                border-radius: 6px;
                cursor: pointer;
                position: relative;
                overflow: hidden;
                &:hover{
                    border-color: #409EFF;
                }
                .avatar-uploader-icon{
                    font-size: 28px;
                    color: #8c939d;
                    width: 178px;
                    height: 178px;
                    line-height: 178px;
                    text-align: center;
                }
                .avatar {
                    width: 178px;
                    height: 178px;
                    display: block;
                }
            }
        }
    }
}
</style>



